<!---->
<template>
 <div>
     <!---->
    <input :value="value" :type="type" :placeholder="placeholder" @keypress="keypress" @keydown="keydown" @input="input" @change="change" @blur="blur" ref="input" class="ex-input ex-input-large ex-input-max" step="0.01" />
    <!--onkeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode || event.which))) || event.which === 8 || event.which === 46"  -->
 </div>
</template>

<script>
//@mousewheel.native.prevent

/**
 * 1
 *
 * <input type="number" @keydown="handleInput" placeholder="" v-model="SaleQty">
 * handleInput(e) {
 * e.target.value=e.target.value.replace(/[^\d]/g,'');
 * },
 */
/**2 */
//onkeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode || event.which))) || event.which === 8 || event.which === 46"
// function noNumbers(e){
//     var keycode = window.event ? e.keyCode : e.which;
//     //Internet Explorer/Chrome  event.keyCode  Netscape/Firefox/Opera  event.which
//     var keychar = String.fromCharCode(keycode);//fromCharCode: Unicode 

//     if(keycode >= 48 && keycode <= 57){     //0-9
//         e.returnValue = true
//     }else if(keycode >=96 && keycode <= 105){       //onkeydown0-9
//         e.returnValue = true;
//     }else if(keycode == 8 || keycode == 46){        //delete
//         e.returnValue = true;
//     }else if(keycode == 45){        //onkeydown:keycode == 109 || keycode == 189
//         e.returnValue = false;
//     }else{            //
//         e.returnValue = false;
//     }
// }
export default {
    name: '',
    model:{
        prop:'value',
        event:'input'
    },
    props: {
        //
        value:{
            type:[String,Number],
            default:''
        },
        //
        type:{
            type:String,
            default:'text'
        },
        //
        maxLength:{
            type:[String,Number],
            default:0
        },
        //
        placeholder:{
            type:String,
            default:''
        },
        //
        isNegative:{
            type:Boolean,
            default:true
        },
        //,-10
        decimal:{
            type:[Number],
            default:0
        }
    },
    components: {

    },
    data () {
        return {

        }
    },
    mounted() {
        this.setNativeInputValue();
    },
    methods: {
        //input
        getInput() {
            return this.$refs.input;
        },
        //
        select() {
            this.getInput().select();
        },
        //dom
        setNativeInputValue() {
            const input = this.getInput();
            input.value = this.value;
        },
        //
        keypress(e){
            //
            // if(!this.isNegative && e.key == '-'){
            //     e.preventDefault();
            // }
        },
        //
        keydown(e){
            //
            // if(!this.isNegative && e.which ==229 ){
            //     console.log(e,'keyup')
            //     e.preventDefault();
            // }
        },
        //
        input(e){
            // if(!this.isNegative){
            //     e.target.value=e.target.value.replace('-','');
            // }
            if(e.target.value){
            //     //
            //     if(this.decimal>0){
            //         let regex=new RegExp(`^\\d+(?:\\.\\d{0,${this.decimal}})?`,"g");
            //         // let regex=new RegExp(`^\\d*(\\.?\\d{0,${this.decimal}})`,"g");     //regExp
            //         e.target.value = (e.target.value.match(regex)[0]) || null;
            //     }
                //
                if(this.maxLength > 0 && e.target.value.length >= this.maxLength){
                    e.target.value = e.target.value.slice(0,this.maxLength);
                }
            }
            this.$emit("input",e.target.value);
            this.$nextTick(this.setNativeInputValue);
            //
            // if(this.decimal>0){
            //     e.target.value = (e.target.value.match(/^\d*(\.?\d{0,2})/g)[0]) || null;
            // }
            // this.$emit("input",e.target.value);
        },
        //
        change(e){
            this.$emit("change",e.target.value);
        },
        //
        blur(e){
            this.$emit("blur",e.target.value);
        }
    },
    watch:{
        value() {
            this.setNativeInputValue();
        },
    }
}
</script>

<style scoped lang='less'>
.ex-input {
    display: inline-block;
    width: 100%;
    height: 32px;
    line-height: 1.5;
    padding: 4px 7px;
    font-size: 14px;
    border: 1px solid #dcdee2;
    border-radius: 4px;
    color: #666;
    background-color: #fff;
    background-image: none;
    position: relative;
    cursor: text;
    transition: border 0.2s ease-in-out, background 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}
.ex-input-large {
    font-size: 16px;
    padding: 6px 7px;
    height: 40px;
}
.ex-input-max {
    background-color: #111;
    border-color: transparent;
    color: #c8c7d8;
    caret-color: #c8c7d8;
}
.ex-input::-webkit-input-placeholder {
    color: #c8c7d8;
}
/**/
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
-webkit-appearance: none;
}

input[type="number"] {
-moz-appearance: textfield;
}
</style>
